@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
:root{
    --primary-color: #8e45ae;
}

*   {
    margin: 0;
    border: 0;
    padding: 0;
    box-sizing: border-box;
}


a {
    color: #fff;
    text-decoration: none;
}

html,body, #root {
    height: 100%;
    width: 100%;
}

h1,h2,h3,h4,h5,h6,p,a,li {
    font-family: 'Roboto',sans-serif;
}


.containerLayout {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    justify-items: center;
}

.containerHeader {
    display: flex;
    flex-direction: column;
    background-color: var(--primary-color);
    justify-content: space-evenly;
    align-items: center;
    height: 500px;

}
.containerHeader #menu-navigation-header {
    display: flex;
    width: 400px;
    font-size: 14px;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    height: 10%;
}
.containerHeader #menu-navigation-header {
    list-style-type: none;
}
.containerHeader #menu-navigation-header li {
    font-family: 'Roboto',sans-serif;
    color: #fff;
    font-weight: 600;
    font-size: 13px;
}
.containerHeader #menu-navigation-header li::after {
    content: '';
    border: 0px 0px 10px 0px solid #fff;  
}
#content-header {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: space-around;
    margin-top: 30px;

}

#content-header h1 {
    margin-top: 10px;
    font-size: 28px;
    margin-bottom: 10px;
    color: #fff;
}
#content-header h3 {
    margin-top: 10px;
    font-size: 15px;
    font-family: sans-serif;
    margin-bottom: 10px;
    color: #fff;
}

hr { 
    width: 40px;
    height: 5px;
    background: #fff;
}
#picture-profile {
    width: 150px;
    border-radius: 6em;
}

#social {
    width: 220px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}
#social i {
    border: 2px solid #fff;
    background-color: transparent;
    padding: 10px;
    width: 40px;
    color: #fff;
    border-radius: 5em;
    cursor: pointer;
}

#instagram:hover {
    background: rgb(116, 11, 67);
    border-color: rgb(116, 11, 67);
    transform: rotate(360deg);
    transition: 0.4s ease-in;
}

 #github:hover {
    background: rgb(3, 1, 2);
    border-color: rgb(3, 1, 2);;
    transform: rotate(360deg);
    transition: 0.4s ease-in;
}
#facebook:hover {
    background: rgb(28, 0, 119);
    border-color:rgb(28, 0, 119);
    transform: rotate(360deg);
    transition: 0.4s ease-in;
}

#linkedin:hover {
    background: rgb(54, 0, 233);
    border-color: rgb(54, 0, 233);
    transform: rotate(360deg);
    transition: 0.4s ease-in;
}



.containerSection {
    display: block;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    width: 95%;
    top: 25px;
    margin: auto;
    border-top-left-radius: 5em;
    border-top-right-radius: 5em;
    height: 850px;
}
#containerNavigationSection {
    list-style-type: none;
    color: rgb(105, 105, 105);
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    background: #fff;
    width: 100%;
}

#containerNavigationSection li {
    margin-left: 16px;
    padding: 10px;
    font-size: 13px;
}

#containerNavigationSection li:hover {
    cursor: pointer;
    border-bottom: 2px solid rgb(138, 138, 138);
}
#containerProjects {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    justify-content: center;
    margin-top: 60px;
    height: 650px;
    overflow: scroll;
}

#containerProjects::-webkit-scrollbar  {
    width: 2px;
}

#containerProjects a { 
    display: flex;
    align-items: center;
}
#containerProjects img {
    width: 95%;
    margin: auto auto 15px auto;
    max-width: 600px;
    height: 265px ;
    object-fit: cover;  

}

@media only screen  and (max-width: 620px){
    #containerProjects {
        grid-template-columns: 1fr;
    }
    #containerProjects img {
        width: 100%;
    }
}
.containerServices {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  background-color: var(--primary-color);
  height: 100%;
}

.containerServices section {
  width: 100%;
  height: 300px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  color: #fff;
}
.containerServices section i {
  font-size: 3em;  
}
.containerServices section  h4 {
  font-size: 2em;
}

.containerServices section p {
  text-align: center;
  width: 60%;
}

@media only screen  and (max-width: 620px){
  .containerServices {
      grid-template-columns: 1fr;
  }
}
#containerAbout {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  height: 800px;
}

#containerAbout #myabout {
  display: flex;
  flex-direction: column;
  width: 500px;
  height: 400px;
  font-family: Roboto, sans-serif;
}
#myabout > h3 ,
#work h3,
#scholl h3  {
  color: #f5a623; 
  font-size: 24px;
  margin-bottom: 20px;
}

.works, .scholls {
  margin-top: 10px;
}

.works h4 ,.scholls h4 {
  color:#555;
  font-weight: 800;
  margin-bottom: 9px;
}

.works, .scholls p {
  color:#555;
}



#myabout div p {
  text-align: justify;
  word-wrap: break-word;
  font-size: larger;
}

#container-work-scholl {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  width: 600px;
  height: 400px;
}

@media only screen and (max-width: 1010px){
  #containerAbout {
    flex-direction: column;
    padding-top: 40px;
  }
}


@media only screen and (max-width: 600px){
  #containerAbout #myabout  {
    width: 100%;
  }
  #container-work-scholl {
    width: 100%;
  }
  #myabout  p,h3 {
    width: 80%;
    font-size: 13px;
    margin: auto;
  }
  .works, .scholls p {
      font-size: 12px;
  }

}


#containerNotification {
  position: absolute;
  top: 0;
  right: 0;
  width: 250px;
  height: 50px;
  background-color: rgb(7, 128, 45);
  opacity: 0.0;
  border-radius: 1em;
}
#containerNotification p {
  text-align: center;
  color: #fff;
  font-family: Roboto;
  margin-top: 13px;
}
@-webkit-keyframes shownotification {
  from {
    top: 0px;
    opacity: 0.0;
  }
  to {
    top: 60px;
    opacity: 1.0;
  }
}
@keyframes shownotification {
  from {
    top: 0px;
    opacity: 0.0;
  }
  to {
    top: 60px;
    opacity: 1.0;
  }
}
.container-anuncio {
    width: 100%;
    max-height:300px;
}
